.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.price-tag{
  position: absolute;
  background-color: #ff8b8c;
  color: #FFFF;
  padding: 5px;
  top:-5px;
  right: -16px;
  font-size: 14px;
}

.card-menu{
  box-shadow: 5px 5px 10px 2px #CCCC ;
}

.ant-table-thead tr th{
  background-color: #ff8b8c!important;
  color:#FFFF;
}

.ant-table-thead tr .merge-header{
  background-color: #FFFF!important;
  border-right: 1px solid #ccc!important;
  color:#FFFF;
}

.status-paid{
  background-color: green;
  color: #FFFF;
  padding:3px 5px;
  border-radius: 5px;
  width: 100%;
}

.status-cancel{
  background-color: cyan;
  color: #FFFF;
  padding:3px 5px;
  border-radius: 5px;
  width: 100%;
}

.status-pending{
  background-color: red;
  color: #FFFF;
  padding:3px 5px;
  border-radius: 5px;
  width: 100%;
}

.status-delivered{
  background-color: #d48806;
  color: #FFFF;
  padding:3px 5px;
  border-radius: 5px;
  width: 100%;
}
.invoice-print{
  font-size: 9px;
  font-family: 'Khmer OS Siemreap';
  /* background-color: red; */
}

.invoice-print tr td{
  padding:2px 5px;
}

.description-order {
  border:0.5px solid #000;
  border-collapse: collapse;
}

.description-order tr:nth-child(odd){
  background-color: #f1f6fa!important;
}

.description-order tr th {
  background-color: #000;
  font-weight: bold;
}

.description-order tr td{
  border-right: 0.2px solid #000;
}

.order-table tr:nth-child(odd){
  background-color: #fff3f3!important;
}


.btn-grad-under {background-image: linear-gradient(to right, #517fa4 0%, #243949  51%, #517fa4  100%)}
  .btn-grad-under {
     text-align: center;
     text-transform: uppercase;
     transition: 0.5s;
     background-size: 200% auto;
     color: white;            
     border-radius: 2px;
     display: block;
     padding: 10px;
      border: 1px solid transparent;
      box-shadow: 0 2px 0 rgb(0 0 0 / 2%);
   }

   .btn-grad-under:hover {
     background-position: right center; /* change the direction of the change here */
     color: #000;
     text-decoration: none;
     /* cursor: pointer; */
   }

   
   .btn-grad {background-image: linear-gradient(to right, #DA22FF 0%, #9733EE  51%, #DA22FF  100%)}
   .btn-grad {
      text-align: center;
      text-transform: uppercase;
      transition: 0.5s;
      background-size: 200% auto;
      color: white;            
      box-shadow: 0 0 20px #eee;
      border-radius: 2px;
      display: block;
      padding: 4px;
      border: 1px solid transparent;
      box-shadow: 0 2px 0 rgb(0 0 0 / 2%);
    }

    .btn-grad:hover {
      background-color: #ff8b8c;
      background-position: right center; /* change the direction of the change here */
      color: #fff;
      text-decoration: none;
      cursor: pointer;
    }
   
  .thanks-container p{
    font-style: italic!important;
  }



